.ead{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
    padding-bottom: 1rem;
    &-form1, &-form2{
        &-icon{
            display: flex;
            padding-left: 1rem;
            padding-top: .3rem;
            flex-direction: row;
            justify-content: space-between;
            gap: .4rem;
            align-items: center;
            &-div{
                display: flex;
                padding-left: 1rem;
                padding-top: .3rem;
                flex-direction: row;

                gap: .4rem;
                align-items: center;
            }
        }
        width: 100%;
        &-form{
            display: flex;
            padding: 1rem;
            flex-direction: column;
            &-block{
                display: flex;
                flex-direction: column;
                input{
                    padding: .875em;
                    border-radius: 4px;
                    border: 1px solid #ccc;
                }
                input:active{
                    outline: none;
                }
                label{
                    margin-top: 1rem;
                }
            }
        }
        &-button{
            margin-top: 1rem;
        }
    }
}

.eadList{
    padding-top: 1rem;
    margin-top: 1rem;
    &-title{
        display: flex;
        flex-direction: row;
        gap: 1rem;
        align-items: center;
    }
}

.product{
    cursor: pointer;
}
.product:hover{
    background-color: hwb(207 81% 0%);
}